import React, { PropTypes } from 'react';
import { withProps } from '../PropsComponent';

const Checkbox = ({ checked, ...props }) => (
	<input type="checkbox" checked={checked || false} {...props} />
);

Checkbox.propTypes = {
	checked: PropTypes.bool,
};

export default withProps(Checkbox);



