import React, { PropTypes } from 'react';
import classNames from 'classnames';

class Tooltip extends React.Component {
	constructor() {
		super();
		this.state = {};
	}

	render() {
		const { className, inline, title, placement = 'top', children } = this.props;

		return (
			<div
				className={classNames('bdp-tooltip clearfix', className)}
				style={{ display: inline !== false ? 'inline-block' : 'block' }}
			>
				{children}

				{title ?
					<div className={classNames('bdp-tooltip-container', placement)}>
						<div className="bdp-tooltip-arrow" />
						<div className="bdp-tooltip-content">
							<div className="bdp-tooltip-inner">{title}</div>
						</div>
					</div> : null
				}
			</div>
		);
	}
}

Tooltip.propTypes = {
	className: PropTypes.string,
	inline: PropTypes.bool,
	title: PropTypes.node,
	placement: PropTypes.string,
	children: PropTypes.node,
};

export default Tooltip;



