import React from 'react';

/**
 * Component need provide { children } container to hold the children list.
 * @param Component
 * @param getList
 */
export const withTree = (Component, getList) => {
	class TreeItem extends React.Component {
		constructor() {
			super();

			this.onClick = this.onClick.bind(this);
		}

		onClick() {
			console.log('TODO: Click', this);
		}

		render() {
			const list = getList(this.props) || [];

			return (
				<Component {...this.props}>
					{list.map((item, index) => (
						<TreeItem key={index} {...item} />
					))}
				</Component>
			);
		}
	}

	return TreeItem;
};

export default {};



