import React, { PropTypes } from 'react';
import { withProps } from '../PropsComponent';

const Th = ({ children, ...props }) => {
	const domProps = Object.assign({}, props);
	delete domProps.styles;

	return <th {...domProps}>{children}</th>;
};

Th.propTypes = {
	children: PropTypes.oneOfType([PropTypes.element, PropTypes.string, PropTypes.number]),
};

export default withProps(Th);



