import React, { PropTypes } from 'react';
import classNames from 'classnames';

import A from '../A';
import Footer from '../Box/Footer';

class Tabs extends React.Component {
	constructor() {
		super();

		this.state = {
			current: 0,
		};

		this.onTabClick = this.onTabClick.bind(this);
	}

	onTabClick(event, props) {
		const current = props['data-index'];
		if (this.state.current !== current) {
			this.setState({ current });
		}
	}

	getList = () => {
		const { children, list } = this.props;
		if (list) return list;

		return children ? React.Children.map(children, (cell = {}) => {
			const { content, children: cellChildren, ...props } = cell.props || {};
			return {
				content: content || cellChildren,
				...props,
			};
		}) : [];
	};

	getCurrentTab() {
		const { current } = this.state;
		const list = this.getList();
		return list[current];
	}

	setCurrentTab(index) {
		this.setState({ current: index });
	}

	render() {
		const {
			className, isPill, isBox, hasContent,
			toolBar, footer, loading, onlyOne,
		} = this.props;
		const { current } = this.state;
		const list = this.getList();
		let $content;

		if (hasContent !== false) {
			$content = (
				<div className="tab-content">
					{list.map((tab, index) => {
						const { content } = tab;
						if (content && (!onlyOne || current === index)) {
							return (
								<div className={classNames('tab-pane', { active: current === index })} key={index}>
									{content}
								</div>
							);
						}
						return null;
					})}
				</div>
			);
		}

		let $footer;
		if (footer) {
			$footer = (footer.type || {})._isBoxFooter ? footer : <Footer>{footer}</Footer>;
		}

		const $loading = loading ? (
			<div className="overlay">
				<span className="fa fa-refresh fa-spin" />
			</div>
		) : null;

		return (
			<div className={classNames(className, 'bdp-tabs', isBox && 'nav-tabs-custom overlay-wrapper')}>
				<ul className={classNames('nav', isPill ? 'nav-pills' : 'nav-tabs')}>
					{list.map(({ name }, index) => (
						<li className={classNames({ active: current === index })} key={index}>
							<A href="javascript:;" onClick={this.onTabClick} data-index={index}>{name}</A>
						</li>
					))}
					{toolBar && (
						<li className="pull-right">{toolBar}</li>
					)}
				</ul>
				{$content}
				{$footer}

				{$loading}
			</div>
		);
	}
}

Tabs.propTypes = {
	className: PropTypes.string,
	isPill: PropTypes.bool,
	isBox: PropTypes.bool,
	hasContent: PropTypes.bool,
	list: PropTypes.arrayOf(PropTypes.shape({
		name: PropTypes.node,
	})),
	toolBar: PropTypes.node,
	footer: PropTypes.node,
	loading: PropTypes.bool,
	children: PropTypes.node,
	onlyOne: PropTypes.bool,
};

export default Tabs;



